@use "../../../App/variables" as *;

.StudentsRow {
    
    &__row-container {
        height: 48px;
        padding: 0 20px;
        display: flex;
        gap: 24px;
        border-radius: 5px 5px 0 0;
        font-size: 14px;
        box-shadow: 0 -1px 3px rgba($color: #f2f2f2, $alpha: 1);
        color: $color-text-gray-main;

        &--header {
            color: $color-gray-mid;
            box-shadow: none;
        }

        &--dark-bg {
            background: $color-bg-dark;
        }
    }

    &--hidden {
        visibility: hidden;
    }

    &__item {
        display: flex;
        flex: 1 0 auto;
        justify-content: space-between;
        text-align: center;
        align-items: center;
        white-space: nowrap;
    }

    &--header > &__item {
        overflow-x: clip;
    }

    &__selected {
        justify-content: center;
        overflow-x: hidden;
    }

    &__button {
        display: flex;
        align-items: center;
        &-icon {
            transition: 0.3s;

            &--expanded {
                transform: rotateX(180deg)
            }
        }
    }

    &__name {
        flex: 1 1 auto;
        width: 300px;
        min-width: 140px;
        overflow-x: auto;
    }

    &__id {
        width: 96px;
    }

    &__class {
        width: 40px;
    }

    &__score {
        width: 100px;
    }

    &__speed {
        width: 112px;
    }

    &__parents {
        width: 384px;
        min-width: 180px;
        flex: 1 1 auto;

        &-wrapper {
            height: 100%;
            overflow-x: auto;
            display: flex;
            align-items: center;
            gap: 4px;
        }

        &-icon {
            &-wrapper {
                display: flex;
                align-items: center;
            }
            margin-bottom: 1px;
            cursor: pointer;
        }
    }
}
